.skills {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    border: 1px solid var(--dark_color);
    background-color: var(--dark_color);
    color: var(--background_color_1);
    border-radius: var(--borderRadiusCustom);
    margin: 0 20px;
    padding: 30px;
}


.skills_container {
    margin: 50px 0 0 0;
    display: flex;
    flex-direction: column;
    align-items: center;

}

.cards_container {
    margin: 50px 0 0 0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
}


/* === MEDIA === */
@media (max-width: 1000px) {
    .skills {
        border: none;
        border-top: 1px solid var(--dark_color);
    }

    .cards_container {
        width: 100%;
    }
}
