.write_me {
    display: flex;
    justify-content: center;
    padding: 0 80px 0 80px;
    min-height: 80vh;
    scroll-behavior: smooth;
    background: url("../../assets/back5.png");
}


.write_me_container {
    color: var(--dark_color);
    background: var(--glass_2);
    backdrop-filter: blur(14px);
    border: 1px solid var(--dark_color);
    width: 600px;
    height: 750px;
    margin: 0 0 40px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    padding: 20px 200px;
    border-radius: var(--borderRadiusCustom);
}

.input_custom {
    border-radius: 10px;
    width: 100%;
    height: 65px;
    padding: 0 0 0 20px;
    margin: 0 0 30px 0;
    border: 1px solid var(--dark_color);
    background-color: var(--background_color_2);
    outline: none;

    font-style: normal;
    font-weight: 300;
    font-size: 30px;
    line-height: 30px;
    display: flex;
    align-items: center;
    color: rgba(28, 33, 33, 0.8);
}

.textarea_custom {
    border-radius: 10px;
    width: inherit;
    height: 250px;
    padding: 20px;
    margin: 0 0 30px 0;
    border: 1px solid var(--dark_color);
    background-color: var(--background_color_2);
    outline: none;
    font-style: normal;
    font-weight: 300;
    font-size: 30px;
    line-height: 30px;
    display: flex;
    align-items: center;
    color: rgba(28, 33, 33, 0.8);
    resize: none;
}

.btn {
    border-radius: var(--borderRadiusCustom);
    cursor: pointer;
    height: 60px;
    color: var(--dark_color);
    width: 100%;
    border: 1px solid var(--dark_color);
    background-color: var(--glass_2);
    transition: 0.3s;
    backdrop-filter: blur(3px);
    font-size: 26px;
}

.btn:hover {
    background-color: var(--dark_color);
    color: var(--background_color_1);
}
.informText {
    display: block;
    margin: 20px;
    font-size: 50px;
}
/* MEDIA */

@media (max-width: 1000px) {
    .write_me {
        height: auto;
        display: flex;
        justify-content: center;
        padding: 0;
        width: 100%;
    }

    .write_me_container {

        padding: 0;
        margin: 0;
        width: 100%;
    }

    .input_custom {
        width: 80%;
        font-size: 1em;
    }

    .textarea_custom {
        width: 80%;
        font-size: 1em;
    }

    .btn {
        width: 90%;
    }
}
