.header {
    position: absolute;
    top: 0;
    z-index: 100;
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: 30px;
    border-bottom: 1px solid var(--dark_color);
    background: var(--glass);
    backdrop-filter: blur(3px);
    -webkit-backdrop-filter: blur(10px);
    transition: ease-in-out 0.5s;

}

.header_mobile {
    display: none;
}

.header_container {
    padding: 0 80px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    transition: ease-in-out 0.5s;
    font-size: 20px;
}

.logo_header_block {
    display: flex;
    align-items: center;

}

.logo {
    cursor: pointer;
    border-radius: 100px;
    border: 1px solid var(--dark_color);
    height: 60px;
    width: 60px;
    margin: 10px;
    transition: ease-in-out 0.5s;
}

.logo:hover {
    transform: rotate(-360deg);
}

.nav a {
    color: var(--dark_color);
    margin: 0 20px;
    font-size: 20px;
    transition: ease-in-out 0.2s;
    position: relative;
    cursor: pointer;
    line-height: 1; /*задаём высоту строки*/
    text-decoration: none; /*убираем подчёркивание*/
}
.nav a:after {
    display: block;
    position: absolute;
    bottom: 0;
    left: 0; /*изменить на right:0;, чтобы изменить направление подчёркивания */
    width: 100%;/*задаём длинну линии до наведения курсора*/
    height: 0; /*задаём ширину линии*/
    background-color: var(--dark_color); /*задаём цвет линии*/
    content: "";
    transition: height 0.1s; /*задаём время анимации*/
}

.nav a:hover:after,
.nav a:focus:after {
    height: 8%;
}

.send_me_button_nav {
    cursor: pointer;
    height: 45px;
    width: 145px;
    border: 1px solid var(--dark_color);
    background-color: var(--background_color_2);
    transition: 0.3s;
    border-radius: var(--borderRadiusCustom);
}

.send_me_button_nav:hover {
    background-color: var(--dark_color);
    color: var(--background_color_1);
}
.open_menu_list_box{
    display: none;
}
.open_menu_list_box a {
    text-decoration: none;
}
.header_mobile_container_open {
    display: none;
    transition: ease-in-out 0.5s;
}

/* MEDIA */

@media (max-width: 1000px) {
    .header {
        display: none;

    }

    .header_mobile {
        position: fixed;
        top: 0;
        z-index: 100;
        width: 100%;
        min-height: 80px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        font-size: 20px;
        border-bottom: 1px solid var(--dark_color);
        background: var(--glass);
        backdrop-filter: blur(3px);
        -webkit-backdrop-filter: blur(10px);
        transition: ease-in-out 0.5s;
    }

    .header_mobile_container {
        padding: 0 20px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        transition: ease-in-out 0.5s;
    }

    .logo {
        width: 40px;
        height: 40px;
        transition: ease-in-out 0.5s;
    }

    .logo_header_block p {
        font-size: 0.8em;
    }

    .div_container {
        display: inline-block;
        cursor: pointer;

    }

    .bar1, .bar2, .bar3 {
        width: 35px;
        height: 3px;
        background-color: #333;
        margin: 6px 0;
        transition: ease-in-out 0.5s;
        border-radius: var(--borderRadiusCustom);
    }

    .header_mobile_open {
        position: fixed;
        top: 0;
        z-index: 200;
        width: 100%;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        font-size: 20px;
        border-bottom: 1px solid var(--dark_color);
        background: var(--glass);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        transition: ease-in-out 0.5s;
    }

    .header_mobile_container_open {
        padding: 50px 20px;
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        transition: ease-in-out 0.5s;
    }

    .bar1_open {

        width: 35px;
        height: 3px;
        background-color: #333;
        margin: -8px 0;
        transition: 0.4s;
        transform: rotate(45deg);
    }

    .bar2_open {
        display: none;
    }

    .bar3_open {
        width: 35px;
        height: 3px;
        background-color: #333;
        margin: 6px 0;
        transition: 0.4s;
        transform: rotate(-45deg);
    }
    .open_menu_list_box {
        display: block;
        padding: 0 20px;
        margin: 0;
    }
    .mobile_menu_item {
        border-radius: 20px;
        display: flex;
        align-items: center;
        font-size: 30px;
        background-color: var(--dark_color);
        color: var(--background_color_1);
        padding: 10px 20px;
        margin: 20px 10px;
        height: 50px;
        transition: ease-in-out 0.5s;
    }

}
